.header {
  background-color: white;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 122px;
  padding-top: 35px;
  padding-bottom: 35px;
  z-index: 1000;
  @media (max-width: 1024px) {
    height: 90px;
  }
  @media (max-width: 768px) {
    height: 70px;
  }
  @media (max-width: 480px) {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .headerContainer {
    max-width: 1200px;
    margin: 0 auto;
    height: 100%;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 768px) {
      display: flex;
      justify-content: space-between;
    }
    @media (max-width: 480px) {
      padding-left: 5px;
      padding-right: 5px;
    }

    .menu {
      margin-left: 150px;
      @media (max-width: 1024px) {
        margin-left: 0px;
      }
      @media (max-width: 768px) {
        position: absolute;
        top: -1000px;
      }
      .menuLinks {
        display: flex;
        list-style: none;

        button {
          margin-right: 65px;
          text-decoration: none;
          font-family: Open Sans;
          font-size: 18px;
          font-style: normal;
          font-weight: 400;
          line-height: normal;
          color: #151618;
          position: relative;
          background-color: transparent;
          border: none;
          cursor: pointer;

          &::before {
            content: "";
            position: absolute;
            width: 0%;
            height: 2px; 
            bottom: -8px;
            left: 0;
            background-color: #151618; 
            transition: width 0.3s ease; 
          }
          &:hover::before,
          &:active::before {
            width: 100%;
          }

          @media (max-width: 1024px) {
            margin-right: 30px;
          }

          &:last-child {
            margin-right: 124px;
            @media (max-width: 1024px) {
              margin-right: 50px;
            }
          }
        }
      }
    }
  }
  .actions {
    display: flex;

    button {
      border: none;
      background-color: white;
      margin-right: 20px;
      &:last-child {
        margin-left: 80px;
      }

      cursor: pointer;
      &:last-child {
        @media (max-width: 1024px) {
          margin-left: 50px;
        }

        @media (max-width: 480px) {
          margin-left: 0px;
          margin-right: 0px;
        }
      }
    }
  }
}
