.explore {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 150px;
  color: #151618;
  @media (max-width: 1000px) {
    margin-top: 120px;
  }
  @media (max-width: 480px) {
    margin-top: 60px;
  }

  .exploreContainer {
    max-width: 1200px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    .exploreTopText {
      font-family: Cormorant;
      font-size: 40px;
      font-style: italic;
      font-weight: 300;
      line-height: normal;
      text-align: center;
      margin: 0px;
      align-self: center;

      @media (max-width: 480px) {
        font-family: Open Sans;
        font-size: 16px;
        font-style: italic;
        font-weight: 300;
        line-height: normal;
      }
    }
    .teaCollections {
      margin-top: 150px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      @media (max-width: 1000px) {
        margin-top: 135px;
      }
      @media (max-width: 480px) {
        margin-top: 60px;
      }

      .teaTitle {
        font-family: Cormorant;
        font-size: 40px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-bottom: 60px;

        @media (max-width: 480px) {
          font-size: 24px;
          margin-bottom: 30px;
        }
      }

      .teaItems {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;

        .teaItem {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;

          span {
            font-family: Cormorant;
            font-size: 24px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
          }
        }
      }
    }
  }
}
